








import { Component, Vue, Watch } from 'vue-property-decorator'

/**
 * @title  更新消息内容
 * @desc 可以通过唯一的 key 来更新内容。
 */
@Component({
  components: {
  },
  })
export default class UpdateExample extends Vue {
  open () {
    this.$notification.open({
      key: 'test001',
      title: 'Loading',
      message: 'loading in action....'
    })
  }

  update () {
    this.$notification.open({
      key: 'test001',
      title: 'Loading',
      message: 'loading completed....'
    })
  }
}
