module.exports = "<template>\n<div>\n   <div class=\"m-3 display-4 text-center\"> {{$vua.breakpoint.name}}</div>\n   <div class=\"m-3\">\n     <p>xs: span-24 gutter-0</p>\n     <p>sm: span-12 gutter-4</p>\n     <p>md: span-8 gutter-8</p>\n     <p>lg: span-6 gutter-16</p>\n     <p>xl: span-4 gutter-32</p>\n      <v-row :gutter=\"gutter\">\n       <v-col :xs=\"xs\" :sm=\"sm\" :md=\"md\" :lg=\"lg\" :xl=\"xl\"><div :class=\"[$style.cell]\"></div></v-col>\n       <v-col :xs=\"xs\" :sm=\"sm\" :md=\"md\" :lg=\"lg\" :xl=\"xl\"><div :class=\"[$style.cell]\"></div></v-col>\n       <v-col :xs=\"xs\" :sm=\"sm\" :md=\"md\" :lg=\"lg\" :xl=\"xl\"><div :class=\"[$style.cell]\"></div></v-col>\n       <v-col :xs=\"xs\" :sm=\"sm\" :md=\"md\" :lg=\"lg\" :xl=\"xl\"><div :class=\"[$style.cell]\"></div></v-col>\n       <v-col :xs=\"xs\" :sm=\"sm\" :md=\"md\" :lg=\"lg\" :xl=\"xl\"><div :class=\"[$style.cell]\"></div></v-col>\n       <v-col :xs=\"xs\" :sm=\"sm\" :md=\"md\" :lg=\"lg\" :xl=\"xl\"><div :class=\"[$style.cell]\"></div></v-col>\n     </v-row>\n   </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\n\n/**\n * @title   响应式\n * @desc 响应式布局, gutter需要借助$vua.breakpoint， 本身不带响应式。\n */\n@Component({\n  components: {\n  },\n  })\nexport default class ReactiveExample extends Vue {\n  xs = {\n    span: 24\n  }\n\n  sm = {\n    span: 12\n  }\n\n  md = {\n    span: 8\n  }\n\n  lg = {\n    span: 6\n  }\n\n  xl = {\n    span: 4\n  }\n\n  get gutter () {\n    const bp = this.$vua.breakpoint\n    if (bp.sm) return 4\n    if (bp.md) return 8\n    if (bp.lg) return 16\n    if (bp.xlOnly) return 32\n    return 0\n  }\n}\n</script>\n\n<style lang=\"scss\" module>\n@import '~@/style/var.scss';\n  .cell {\n    height: 36px;\n    line-height: 36px;\n    background-color: $primary-color;\n    text-align: center;\n    color: #fff;\n    margin: .5rem 0;\n  }\n\n  .light {\n    background-color: map-get($primary-colors, lighten-2)\n  }\n</style>\n"