













import { Component, Vue, Watch } from 'vue-property-decorator'

/**
 * @title   去抖
 * @desc 点击去抖， debounce 非 0 正整数
 */
@Component({
  components: {
  },
  })
export default class DebounceExample extends Vue {
  debounce: number = 0

  pTime: number = Date.now()

  cTime: number = Date.now()

  get interval (): number {
    let t = this.cTime - this.pTime
    return t > 0 ? t : 0
  }

  onClick () {
    this.cTime = Date.now()
  }

  onParentClick () {
    this.pTime = Date.now()
  }
}
