module.exports = "<template>\n<div>\n   <div class=\"my-3\">\n      <p>\n         <input type=\"number\" step=\"1000\" min=\"1000\" v-model.number=\"config.duration\"> &nbsp;\n         <input type=\"number\" step=\"1\" min=\"1\" v-model.number=\"config.maxCount\"> &nbsp;\n      </p>\n     <v-button @click=\"info\">display</v-button>  &nbsp;\n   </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\n\n/**\n * @title  全局配置\n * @desc 提供 config 方法， 用于配置message的最大显示数量、自动关闭时间等。\n */\n@Component({\n  components: {\n  },\n  })\nexport default class GlobalExample extends Vue {\n   config: any = {\n      duration: 3000,\n      maxCount: 2\n   }\n\n   info () {\n     this.$message.info('this is a normal message')\n   }\n\n   @Watch('config', {deep: true}) configChange (config: any) {\n     this.$message.config(config)\n   }\n}\n</script>\n"