


























import { Component, Vue, Watch } from 'vue-property-decorator'

/**
 * @title  新增和关闭页签
 * @desc 只有卡片样式的页签支持新增和关闭选项, 使用 closable启用。
 */
@Component({
  components: {
  },
  })
export default class ClosableExample extends Vue {
  value: string = '1'

  position: string = 'top'

  type="card"

  tabs: any[] = []

  index: number = 10

  add () {
    let i = this.index++
    this.tabs.push({
      name: i + '',
      label: 'Label ' + i
    })
  }
}
