module.exports = "<template>\n<div>\n   <div class=\"my-3\">\n      <v-button @click=\"success\" color=\"primary\">success</v-button> &nbsp;\n      <v-button @click=\"info\" color=\"info\">info</v-button> &nbsp;\n      <v-button @click=\"warning\" color=\"warning\">warning</v-button> &nbsp;\n      <v-button @click=\"error\" color=\"error\">error</v-button> &nbsp;\n      <v-button @click=\"loading\">loading</v-button>\n   </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\n\n/**\n * @title  类型\n * @desc 提供五中内置类型，每种类型有特定的图标与颜色。\n */\n@Component({\n  components: {\n  },\n  })\nexport default class TypeExample extends Vue {\n  info () {\n    this.$message.info('this is a message of info')\n  }\n\n  success () {\n    this.$message.success('this is a message of success')\n  }\n\n  warning () {\n    this.$message.warning('this is a message of warning')\n  }\n\n  error () {\n    this.$message.error('this is a message of error')\n  }\n\n  loading () {\n    this.$message.loading('this is a message of loading')\n  }\n}\n</script>\n"