







































































import { Component, Vue, Watch } from 'vue-property-decorator'

/**
 * @title   基本使用示例
 * @desc
 */
@Component({
  components: {
  },
  })
export default class BasicExample extends Vue {
  property: string = ''
    side: string = ''
    breakpoint: string = ''
    size: string = ''

    get spacingCls () {
      return `${this.property}${this.side}${this.breakpoint ? '-' : ''}${this.breakpoint}-${this.size}`
    }
}
