




















import { Component, Vue, Watch } from 'vue-property-decorator'

/**
 * @title   响应式
 * @desc 响应式布局, gutter需要借助$vua.breakpoint， 本身不带响应式。
 */
@Component({
  components: {
  },
  })
export default class ReactiveExample extends Vue {
  xs = {
    span: 24
  }

  sm = {
    span: 12
  }

  md = {
    span: 8
  }

  lg = {
    span: 6
  }

  xl = {
    span: 4
  }

  get gutter () {
    const bp = this.$vua.breakpoint
    if (bp.sm) return 4
    if (bp.md) return 8
    if (bp.lg) return 16
    if (bp.xlOnly) return 32
    return 0
  }
}
